import instance from "../Config/axios"

export function NovedadesResumen(Mes, Ao, Zona,Tipo){
    return instance.get(`lineasBase/novedadesResumen/${Mes}-${Ao}-${Zona}-${Tipo}`)
}

export function NovedadesDetalle(Mes, Ao, Zona,Tipo){
    return instance.get(`lineasBase/novedadesDetalle/${Mes}-${Ao}-${Zona}-${Tipo}`)
}